body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(252, 249, 249);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: rgb(54, 51, 51);
}

li {
  float: left;
}

li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

li a:hover {
  background-color: rgb(228, 98, 98);
  color: black;
  text-decoration: none;
}

.centralt-table {
  border: solid 1px #b5b6b6;
  border-collapse: collapse;
  border-spacing: 0;
  font: normal 13px Arial, sans-serif;
  margin-left: auto;
  margin-right: auto;
}
.centralt-table thead th {
  background-color: #363535;
  border: solid 1px #363535;
  color: #ffffff;
  padding: 10px;
  text-align: left;
}
.centralt-table tbody td {
  border: solid 1px #e7e5e5;
  color: #292a3e;
  padding: 10px;
  text-shadow: 1px 1px 1px #fff;
}
.rubrik {
  text-align: center;
  color: #4b4d4d;
}
p {
  text-align: center;
}
.text-link {
  color: inherit;
  text-decoration: inherit;
}
.text-link:hover {
  background-color: none;
  text-decoration: none;
  color: white;
}
.Home-main-card {
  width: 320px;
  height: 530px;
}
.Home-Card-img {
  height: 350px;
  width: 320px;
}
.CardDeck-Home {
  padding-left: 30px;
  padding-right: 30px;
}

.FortniteShop-main-card {
  width: 90%;
}
.nav-link-brand {
  font-family: aq;
}

@font-face {
  font-family: "aq";
  src: url(Fonts/Aq.otf);
  font-style: normal;
  font-weight: bold;
  font-size: 1rem;
  /* Länk till fonten: https://www.fontspace.com/aquire-font-f43735 */
}
.rubrik_home {
  font-family: "Nanum Gothic", sans-serif;
  color: rgb(59, 59, 59);
  font-weight: bold;
  font-size: 4rem;
}

.rubrik_home_div {
  margin-bottom: 15px;
  margin-top: 15px;
}
